@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;900&display=swap);
body {
	margin: 0;
	font-family: Montserrat;
}

.MoviePage {
	position: relative;
	height: 100vh;
	width: 100%;
	color: #fbfbfb;
}

.watch-trailer {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 10000;
	visibility: hidden;
	opacity: 0;
	background-color: rgba(0, 0, 0, 0.8);
	transition: opacity 0.5s;
}

.fade {
	visibility: visible;
	opacity: 1;
}

.modal {
	top: 100px;
	right: 0;
	left: 0;
	bottom: 100px;
	width: 100%;
	min-width: 320px;
	max-width: 1024px;
	z-index: 10001;
	display: block;
	position: absolute;
	transition: opacity 500ms ease-in;
	margin: 0 auto;
	padding: 24px;
	border: 1px solid rgba(120, 120, 120, 0.7);
	border-radius: 5px;
	background: rgba(60, 63, 65, 0.9);
	background-clip: padding-box;
	box-shadow: 0 0 13px 3px rgba(0, 0, 0, 0.5);
}

.video {
	display: -webkit-flex;
	display: flex;
	height: 100%;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.video iframe {
	width: 100%;
	height: 100%;
}

.close {
	position: absolute;
	right: 18px;
	top: 18px;
	z-index: 999999;
	cursor: pointer;
	background: transparent;
	outline: none;
	border: 2px solid #fbfbfb;
	border-radius: 40px;
	color: #fbfbfb;
}

.header {
	background: #111111;
	box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.25);
}

.header-inner {
	display: -webkit-flex;
	display: flex;
	width: 1140px;
	margin: auto;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
}

.header-logo {
	color: #fefefe;
	font-weight: 300;
	font-size: 24px;
	padding: 35px 0 35px 0;
	text-decoration: none;
}

.header-input {
	position: relative;
}

.header-input i {
	position: absolute;
	top: 15px;
	left: 30px;
}

.header-input input {
	width: 420px;
	padding: 15px 0 15px 68px;
	background: #1b1919;
	border-radius: 30px;
	border: none;
	outline: none;
	font-weight: 500;
	font-size: 18px;
	color: #979191;
}

.header-input input::-webkit-input-placeholder {
	font-weight: 400;
	font-size: 18px;
	color: #5f5f5f;
}

.MovePage-wrapper {
	position: relative;
	height: 810px;
	background: linear-gradient(
		0deg,
		rgba(17, 17, 17, 0.3),
		rgba(17, 17, 17, 0.3)
	);
}

.MovePage-bg {
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: -1;
}

.MovePage-nothing {
	text-align: center;
}

.MovePage-inner {
	width: 1140px;
	margin: auto;
	padding-top: 120px;
}

.title {
	width: 566px;
	font-weight: 900;
	font-size: 72px;
	margin-bottom: 30px;
	color: #fbfbfb;
}

.desc {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	margin-bottom: 197px;
}

.desc .rating {
	font-weight: 900;
	font-size: 12px;
	margin-right: 24px;
}

.desc .genre,
.desc .type {
	font-weight: 600;
	font-size: 24px;
	border-right: 1px solid;
	padding-right: 24px;
	margin-right: 25px;
}

.desc .year {
	font-weight: 600;
	font-size: 24px;
}

.watch {
	padding: 23px 70px;
	background: transparent;
	outline: none;
	border: 2px solid #fbfbfb;
	box-sizing: border-box;
	border-radius: 40px;
	font-weight: 600;
	font-size: 18px;
	color: #fbfbfb;
	margin-bottom: 50px;
	cursor: pointer;
}

.watch:hover {
	border: 2px solid #afa6a6;
	color: #afa6a6;
	background: #000;
}

.awards {
	font-weight: 600;
	font-size: 18px;
	color: #fbfbfb;
	margin-bottom: 100px;
}

.MovePage-text {
	width: 1140px;
	padding-top: 60px;
	margin: auto;
	color: #323232;
}

.MovePage-text__title {
	width: 800px;
	font-weight: 900;
	font-size: 36px;
	margin-bottom: 30px;
}

.MovePage-text__plot {
	width: 800px;
	font-weight: normal;
	font-size: 18px;
	line-height: 28px;
	margin-bottom: 60px;
}

.MovePage-text__like {
	font-weight: 900;
	font-size: 24px;
	margin-bottom: 20px;
}

.MovePage-text__likesMove {
	display: -webkit-flex;
	display: flex;
	width: 1140px;
	height: 360px;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	margin-bottom: 60px;
}

.MovePage-text__likesMove img {
	border-radius: 15px;
}

.footer {
	background: #111111;
	box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.25);
}

.footer-inner {
	display: -webkit-flex;
	display: flex;
	width: 1140px;
	padding: 39px 0;
	margin: auto;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	font-weight: 900;
	font-size: 18px;
	color: #fefefe;
	text-decoration: none;
}

.rating {
	display: -webkit-flex;
	display: flex;
	font-weight: 900;
	font-size: 12px;
	background-color: #fac539;
	color: #000000;
	padding: 8px 12px;
	border-radius: 8px;
	width: 87px;
	height: 30px;
	box-sizing: border-box;
}

.rating .imdb {
	margin-right: 8px;
}

.like_1 {
	position: relative;
	width: 260px;
	height: 360px;
}

.like_1 img {
	width: 100%;
	height: 100%;
}

.like_1:hover .like_1--inner {
	display: -webkit-flex;
	display: flex;
}

.like_1--inner {
	display: none;
	position: absolute;
	-webkit-flex-direction: column;
	        flex-direction: column;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	color: #ffffff;
	padding: 30px;
	border-radius: 15px;
	background: linear-gradient(
		0deg,
		rgba(20, 19, 19, 0.85),
		rgba(20, 19, 19, 0.85)
	);
	cursor: pointer;
}

.like_1--inner .title {
	font-weight: 800;
	font-size: 24px;
	margin-bottom: 8px;
}

.like_1--inner .genre {
	font-weight: 500;
	font-size: 14px;
	margin-bottom: 8px;
}

.like_1--inner .type {
	font-weight: 500;
	font-size: 14px;
	margin-bottom: 21px;
}

.like_1--inner .plot {
	font-weight: normal;
	font-size: 14px;
	line-height: 18px;
	height: 150px;
	overflow: hidden;
}

.like_1--inner .rating {
	position: absolute;
	bottom: 30px;
}

.movie {
	display: -webkit-flex;
	display: flex;
	width: 555px;
	margin: auto;
	background: #111111;
	opacity: 0.8;
	border-radius: 10px;
	box-sizing: border-box;
	padding: 15px 20px 15px 15px;
}

.movie--image {
	min-width: 95px;
	height: 141px;
	margin: 0;
	margin-right: 20px;
	cursor: pointer;
	text-decoration: none;
}

.movie--image img {
	max-width: 100%;
	max-height: 100%;
	border-radius: 10px;
}

.movie--title {
	width: 100%;
	padding-top: 15px;
	text-align: left;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.move--title_desc {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.movie--title_desc-name {
	font-weight: 700;
	font-size: 24px;
	/* margin-bottom: 13px; */
	cursor: pointer;
	text-decoration: none;
	color: #fefefe;
}
.movie--title_desc-text {
	display: -webkit-flex;
	display: flex;
	font-weight: 400;
	font-size: 14px;
	margin: 13px 0 15px 0;
}
.movie--title_desc-text div {
	margin-right: 10px;
}

.movie--title_desc-text .type,
.movie--title_desc-text .genre {
	border-right: 1px solid;
	padding-right: 15px;
}

.movie--title_awards {
	width: 300px;
	font-weight: 400;
	font-size: 12px;
	margin-top: 15px;
}

.movie--title_awards .wrapper {
	display: -webkit-flex;
	display: flex;
}

.movie--title_awards .wrapper .top {
	padding-right: 6px;
	border-right: 1px solid;
}

.movie--title_awards .wrapper .won {
	padding-left: 6px;
}

.SearchPage {
	display: -webkit-flex;
	display: flex;
	position: relative;
	height: 97vh;
	-webkit-justify-content: center;
	        justify-content: center;
	color: white;
	background: linear-gradient(
		0deg,
		rgba(17, 17, 17, 0.3),
		rgba(17, 17, 17, 0.3)
	);
	/* background-size: 100%; */
}

.SearchPage .bg {
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: -1;
}

.bg iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	pointer-events: none;
}

.SearchPage-wrapper {
	padding-top: 30px;
	text-align: center;
	width: 730px;
}

.SearchPage-wrapper h1 {
	font-size: 64px;
	margin-bottom: 40px;
	line-height: 78px;
}

.text {
	font-size: 36px;
	line-height: 52px;
	margin-bottom: 80px;
}

.search {
	width: 555px;
	display: -webkit-flex;
	display: flex;
	position: relative;
	margin: auto;
	margin-bottom: 20px;
}

.search-input {
	padding: 14px 0 14px 28px;
	width: 100%;
	font-weight: 400;
	font-size: 18px;
	color: #312e2e;
	background-color: #fff;
	outline: none;
	border: none;
	border-radius: 30px;
}

.search-input::-webkit-input-placeholder {
	color: #cccc;
}

.search-btn {
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	padding: 14px 28px;
	border: none;
	border-radius: 0 30px 30px 0;
	background: #4ea7f9;
	font-weight: 700;
	font-size: 18px;
	color: #fefefe;
}

